.about__container{
    display: grid;
    grid-template-columns: 50% 50%;
    gap: 15%
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(55deg,transparent, var(--color-primary),transparent);
    display:grid;
    place-items:center;
}

.about__me-image{
    border-radius: 2rem;
    overflow:hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    place-items:center;
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
}

.about__card {
    /* background: var(--color-bg-variant); */
    /* background: rgb(21, 177, 177); */
    background: #62b0b8;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 0.5rem;
    text-align: center;
}

.about__card:hover{
    /* background: transparent; */
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: rgb(60, 18, 100);
    font-size: 1.2rem;
    margin-bottom: 0.15rem;
}

.about__card h5{
    font-size: 0.95rem;;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin : 2rem 0 2.6rem;
    color: rgb(0, 31, 43);
}
h5,h2{
    color:whitesmoke;
}
@media screen and (max-width :1024px){
    .about__container{
      grid-template-columns : 1fr;
      gap:0;
    }

    .about__ content p {
        margin: 1rem 0 1.5rem;
       
    };
}

@media screen and (max-width :600px){
    .about__me{
      width : 65%;
      margin: 0 auto 3rem;
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
    }
}