header {
    height: 75vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* -------------------CTA------------------------------*/

.cta{
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    display: flex;
    gap: 1.5rem;
    justify-content: center;
}

/* -------------------HEADER SOCIAL------------------------------*/

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content:"";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
.qa{
    color: rgb(164, 168, 143);
}

/* -------------------ME------------------------------*/

.me{
    background: var(--color-primary);
    width: 15rem;
    height: 15rem;
    position: relative;
    /* margin-top: 100%; */
    /* left:calc(60%-11rem); */
    /* margin-left: 38rem; */
    overflow: hidden;
    border-radius: 50%;
    margin:auto;
    padding : 5rem 1.5 rem 1.5rem 1.5rem;
    display: block; 
    opacity:1;
    place-items: center;
}

.me:hover{
    opacity:0.5;
}
.scroll__down{
    position: absolute;
    right:-2.3rm;
    bottom: 5rem;
    transform: rotateX(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

.typewriter
{
  background-image: linear-gradient(
    -225deg,
    yellow 0%,
    rgb(102, 4, 78) 29%,
    pink 67%,
    rgb(180, 122, 14) 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 3s alternate infinite;
  display: inline-block;
}

h5, h1, h2{
    color:black;
}
@keyframes textclip {
  to {
    background-position: 75% center;
  }
}

@media screen and (max-width :1024px){
    header{
        height: 68vh;
    }

}


@media screen and (max-width :600px){
    header{
        height: 100vh;
    }

    .header__social,.scroll__down{
        display: none;
    }
}


