.skill__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10rem;
    align-items: center;
}

.skill__container >div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}

.skill__container >div:hover{
    /* background: transparent; */
    border-color: var(--color-primary-variant);
    cursor:default;
}

.skill__container >div h3{
    text-align: center;
    margin-bottom: 2rem;
    /* color: var(--color-primary); */
    color:white;
}

.skill__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap:2rem;
    align-items: center;
}

.skill__details{
    display: flex;
    gap:1rem;
}

.skill__details-icon{
    color:rgb(60, 18, 100);
}

h4{
    color:rgb(6, 62, 80);
}


@media screen and (max-width :1024px){
    .skill__container{
      grid-template-columns : 1fr;
      gap:0;
    }

    .skill__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    };

    .skill__content{
        padding: 1rem;
    }
}

@media screen and (max-width :600px){
    .skill__container{
        grid-template-columns : 1fr;
        gap: 1rem;
      }

    .skill__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }

}